import {useState, useRef, FC, FormEvent} from 'react';
import {Button, EmailInput, Input} from '@ya.praktikum/react-developer-burger-ui-components';
import {NavLink} from "react-router-dom";
import {login} from "../../services/actions/user";
import loginPageStyles from './login.module.css';
import {useForm} from "../../services/hooks/useForm";
import {useAppDispatch} from "../../services/hooks/hooks";

export const LoginPage: FC = () => {
  const {values, handleChange} = useForm({email: '', password: ''});
  const formRef = useRef<HTMLFormElement>(null);
  const dispatch = useAppDispatch();

  const onIconClick = () => {
    if (formRef && formRef.current) {
      formRef.current.focus();
    }
  }

  const handleLoginSubmit = (evt: FormEvent) => {
    evt.preventDefault();
    dispatch(login(values))
  }


  return(
    <>
      <div className={loginPageStyles.formWrapper}>
        <form onSubmit={handleLoginSubmit} ref={formRef} className={loginPageStyles.container}>
          <h2 className='text text_type_main-medium'></h2>
          <EmailInput
            onChange={handleChange}
            value={values.email}
            name={'email'}
            isIcon={false}
          />

          <Input
            type={'text'}
            placeholder={'Password'}
            onChange={handleChange}
            icon={'ShowIcon'}
            value={values.password}
            name={'password'}
            error={false}
            onIconClick={onIconClick}
            errorText={''}
            size={'default'}
            extraClass="ml-1"
          />

          <Button htmlType="submit" type="primary" size="medium">
            
          </Button>
        </form>

        <div className={loginPageStyles.navLinkWrapper}>
          <div className={loginPageStyles.questionWrapper}>
            <p className='text text_type_main-small text_color_inactive'>  ?</p>
            <NavLink to={"/register"}>
              <Button htmlType="button" type="secondary" size="small"></Button>
            </NavLink>
          </div>
          <div className={loginPageStyles.questionWrapper}>
            <p className='text text_type_main-small text_color_inactive'> ?</p>
            <NavLink to={"/forgot-password"}>
              <Button htmlType="button" type="secondary" size="small"> </Button>
            </NavLink>
          </div>

        </div>
      </div>
    </>
  )
}